/**
 * @file response.js
 * @desc {{description}}{{}}
 * @createTime 2018110323:16:47
 */
module.exports = function (instance, config) {
  var objType = function (obj) {
    return Object.prototype.toString.call(obj)
      .replace('[object ', '')
      .replace(']', '')
      .toLowerCase()
  }
  // response 
  instance.interceptors.response.use(
    function (response) {
      // IE9response.dataundefinedresponse.request.responseText(Stringify)
      var data
      if (!response.data) {
        data = response.request.responseText
      }
      else {
        data = response.data
      }

      var oldDataType = objType(data)
      var oldData = data
      try {
        // iedata[object String]
        data = data && oldDataType === 'string' ? JSON.parse(data) : data
      }
      catch (e) {
        data = oldData
        console.error('interceptors response parse data exits error')
        console.error(e)
      }

      // 
      var fileTypes = ['string', 'blob']
      var dataType = objType(data)

      if (~fileTypes.indexOf(dataType)) {
        return {
          data,
          // headersfilename
          fileName: response.headers['filename']
        }
      }
      config.handlers && config.handlers.data && config.handlers.data(data)

      return data
    },
    function (err) {
      if (err && err.response) {
        switch (err.response.status) {
          case 400:
            err.message = ''
            break

          case 401:
            err.message = ''
            break

          case 403:
            err.message = ''
            break

          case 404:
            err.message = `: ${err.response.config.url}`
            break

          case 408:
            err.message = ''
            break

          case 500:
            err.message = ''
            break

          case 501:
            err.message = ''
            break

          case 502:
            err.message = ''
            break

          case 503:
            err.message = ''
            break

          case 504:
            err.message = ''
            break

          case 505:
            err.message = 'HTTP'
            break

          default:
        }
      }
      console.error(err)

      config.handlers && config.handlers.code && config.handlers.error(err)

      return Promise.reject(err) // 
    }
  )
  return instance
}
